import { Component, OnInit } from '@angular/core';
import { NavController, LoadingController, AlertController } from '@ionic/angular';
import { AppMin, myLocalStorage } from '../core';
import { Router, ActivatedRoute } from '@angular/router';



@Component({
  selector: 'app-my-set-service-data',
  templateUrl: './my-set-service-data.page.html',
  styleUrls: ['./my-set-service-data.page.scss'],
})
export class MySetServiceDataPage implements OnInit {
  loader;
  serviceExistence;
  serviceState: boolean;
  serviceShortMessage: boolean; //新订单短信通知
  serviceSystemNotification: boolean;//新订单系统通知
  header_class;
  constructor(public appMin: AppMin, public alertCtrl: AlertController, public ActivatedRoute: ActivatedRoute, public Router: Router, public navCtrl: NavController, public loadingCtrl?: LoadingController) {

    if (this.appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class_md";
      console.log("判断为安卓系统 设置状态栏样式")
    }


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MySetServiceDatePage');


  }
  ionViewWillEnter() {
    //每次即将加载页面

  }
  ionViewDidEnter() {
    //每次已经进入页面

  }
  async  send(value, isshow?: boolean) {
    if (isshow) {
      this.loader = await this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000
      });
      await this.loader.present();
    }


    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


  }

  ischecked() {
    //切换营业按钮
    let obj = {
      type: "setServiceState",
      serviceState: (this.serviceState == true) ? 1 : 0,
      userId: this.appMin.getUserid()
    }

    this.send(JSON.stringify(obj), false);
    console.log("修改营业状态事件");
  }
  ischeckedShort() {
    let obj = {
      type: "setServiceShortMessage",
      serviceShortMessage: (this.serviceShortMessage == true) ? 1 : 0,
      userId: this.appMin.getUserid()
    }
    this.send(JSON.stringify(obj));
  }
  ischeckedSystem() {
    let obj = {
      type: "setServiceSystemNotification",
      serviceSystemNotification: (this.serviceSystemNotification == true) ? 1 : 0,
      userId: this.appMin.getUserid()
    }
    this.send(JSON.stringify(obj));
  }
  myserviceset_click() {
    //设置服务按钮
    // this.navCtrl.push(MySetServiceSerectPage);
    console.log("myserviceset_click")
    this.Router.navigate(['my-set-service-serect'])
  }
  addMySetServiceDatePage() {
    //加载初始数据
    //需要加载是否已经填写服务项目已经完善资料

    let obj = {
      type: "addMySetServiceDatePage",
      userId: this.appMin.getUserid()

    }
    this.send(JSON.stringify(obj), false);

  }
  drawingAccount_click() {
    //提款账户按钮
    //  this.navCtrl.push(DrawingAccountPage);
    this.Router.navigate(['drawing-account'], { queryParams: {} })


  }
  async presentAlert(val) {
    let alert = await this.alertCtrl.create({
      // header: '提示',
      message: val,
      buttons: ['确定']
    });
    alert.present();
  }

  dataHandle(meg: any) {
    // console.log(JSON.stringify(meg));
    if (meg['type'] == "addMySetServiceDatePage_ret") {
      myLocalStorage.setItem('addMySetServiceDatePage_ret', JSON.stringify(meg), 30);
      this.serviceExistence = (meg["serviceExistence"] == 0) ? false : true;
      this.serviceState = (meg["serviceState"] == 0) ? false : true;
      this.serviceShortMessage = (meg['serviceShortMessage'] == 0) ? false : true;
      this.serviceSystemNotification = (meg['serviceSystemNotification'] == 0) ? false : true;
    } else if (meg['type'] == 'setServiceState_ret') {
      if (meg.hasOwnProperty('msg')) {
        if (meg['msg'] == '-1') {
          //未找到记录
             this.presentAlert("请先添加占卜服务项目才能开始营业");
             this.serviceState=false;
             

        }
      }
    }
  }

  ngOnInit() {
    if (myLocalStorage.getItem('addMySetServiceDatePage_ret') != null) {
      this.dataHandle(JSON.parse(myLocalStorage.getItem('addMySetServiceDatePage_ret')))
    }
    this.addMySetServiceDatePage();
  }

}
